import React from 'react';
import './AboutProject.css';

const descriptions = {
  mk: `         ,       ,   .    ,          .       ,  ,               .        ,      .             .                   ,                 .                      ,               .           .            ,       .       ,           . ,                   .         ,       ,               ,                .`,
  en: `Traditional Macedonian folk dances (ora) are endangered due to the lack of a centralized, interactive, and accessible system for their documentation, learning, and dissemination. Existing resources are fragmented, non-interactive, and do not engage younger generations or global enthusiasts. This problem affects cultural institutions, dance groups, educational centers, and individuals striving to preserve and transmit these traditions. There is a lack of a modern platform that unites historical data, multimedia resources, and community features. These factors lead to the loss of cultural heritage and limited access to learning. This negatively impacts cultural institutions and dance groups, which face difficulties in systematic archiving and promotion, as well as teachers and choreographers who lack effective interactive tools for teaching dance steps and techniques. Students and enthusiasts are limited in self-learning or joining folklore groups due to the lack of accessible resources and opportunities to connect with dance communities, while tourists and researchers miss centralized information about regional dance variations and their historical significance. All these factors threaten the sustainability and global reach of dance heritage. The problem is the lack of digital infrastructure for preserving Macedonian folk dance, which affects cultural identity and education. Despite existing physical archives and local initiatives, these are not adapted to modern digital needs and global reach. Therefore, the goal is to develop a digital platform that will address the lack of infrastructure for preserving Macedonian folk dance. The platform will connect traditional knowledge with modern technologies, enable access for a wider global community, support multilingualism, and serve as an educational tool for schools and dance groups, as well as a resource for tourists and researchers seeking a deeper understanding of Macedonian cultural identity.`,
  si: `Tradicionalni makedonski ljudski plesi (ora) so ogroeni zaradi pomanjkanja centraliziranega, interaktivnega in dostopnega sistema za njihovo dokumentiranje, uenje ter irjenje. Obstojei viri so razdrobljeni, neinteraktivni in ne vkljuujejo mlajih generacij ali globalnih navduencev. Problem se pojavlja v kulturnih institucijah, plesnih skupinah, izobraevalnih centrih in med posamezniki, ki poskuajo ohraniti in prenaati te tradicije. Manjka moderna platforma, ki bi zdruevala zgodovinske podatke, multimedijske vire in skupnostne monosti. Ti dejavniki vodijo do izgube kulturne dediine in omejenega dostopa do uenja. To negativno vpliva na kulturne institucije in plesne skupine, ki se sooajo s teavami pri sistematinem arhiviranju in promociji plesov, ter uitelje oziroma koreografe, ki ne morejo uinkovito pouevati plesnih korakov in tehnik, saj jim primanjkuje interaktivnih orodij. tudenti in navduenci so omejeni pri samostojnem uenju ali obiskovanju folklornih skupin zaradi pomanjkanja dostopnih virov in monosti povezovanja s plesnimi skupnostmi, medtem ko turisti in raziskovalci pogreajo centralizirane informacije o regionalnih razliicah plesov in njihovi zgodovinski pomembnosti. Vsi ti dejavniki skupaj ogroajo trajnost plesne dediine in njen globalni doseg. Problem zajema pomanjkanje digitalne infrastrukture za ohranjanje makedonskega ljudskega plesa, kar vpliva na kulturno identiteto in izobraevanje. Kljub obstojeim fizinim arhivom in lokalnim pobudam ti niso prilagojeni sodobnim digitalnim potrebam in globalnemu dosegu. Za ta namen elimo razviti digitalno platformo, ki bo reila pomanjkanje infrastrukture za ohranjanje makedonskega ljudskega plesa. Platforma naj bi povezala tradicionalno znanje s sodobnimi tehnologijami, omogoila dostop iri globalni skupnosti, kar bi lahko vkljuilo podporo z strani jezika/vejezinosti, in bi sluila kot izobraevalno orodje za ole in plesne skupine ter kot vir za turiste in raziskovalce, ki elijo globlje razumeti makedonsko kulturno identiteto.`
};

export default function AboutProject() {
  
  return (
    <div className="about-project">
      <h2>About the Project /   / O projektu</h2>
      <section>
        <h3></h3>
        <p>{descriptions.mk}</p>
      </section>
      <section>
        <h3>English</h3>
        <p>{descriptions.en}</p>
      </section>
      <section>
        <h3>Slovenina</h3>
        <p>{descriptions.si}</p>
      </section>
    </div>
  );
}
